<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="数据表名称">
          <el-input
            v-model="listQuery.table_name"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="工站">
          <el-input
            v-model="listQuery.work_code"
            placeholder="请输入"
          ></el-input>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>人员列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增表</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <!-- 新增物料弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="1000px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-row>
          <div>
            <p class="dialog-title">基本信息</p>

            <el-row :gutter="20">
              <el-col :span="12">
                <el-form-item label="表名称" prop="table_name">
                  <el-input
                    v-if="dialogTitle == '新增数据表'"
                    v-model="dialogForm.table_name"
                  ></el-input>
                  <span v-else>{{dialogForm.table_name}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="工站" prop="work_code">
                  <el-select
                    v-model="dialogForm.work_code"
                    placeholder="请选择"
                    style="width: 100%"
                  >
                    <el-option
                      v-for="(item, index) in WorkCodeNameList"
                      :key="index + ' dialogForm'"
                      :label="item.work_id+'_'+item.work_name"
                      :value="item.work_code"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>

            <el-form-item label="备注" prop="description">
              <el-input
                type="textarea"
                v-model="dialogForm.description"
              ></el-input>
            </el-form-item>
          </div>
          <div>
            <p class="dialog-title">字段明细</p>
            <el-button type="primary" size="small" plain @click="AddListRow()"
              >添加字段</el-button
            >
            <el-table
              :data="dialogForm.response_datas"
              style="width: 100%"
              class="dialogTable"
              height="350"
            >
              <el-table-column fixed="left" label="字段名称" align="center">
                <template slot-scope="scope">
                  <el-form-item
                    :prop="'response_datas.' + scope.$index + '.field_name'"
                    :rules="[{ required: true, message: '字段名称不能为空' }]"
                  >
                    <el-input v-model="scope.row.field_name"></el-input>
                  </el-form-item>
                </template>
              </el-table-column>

              <el-table-column label="字段类型" align="center">
                <template slot-scope="scope">
                  <el-form-item
                    :prop="'response_datas.' + scope.$index + '.field_type'"
                    :rules="[{ required: true, message: '类型不能为空' }]"
                  >
                    <el-select
                      v-model="scope.row.field_type"
                      placeholder="请选择"
                    >
                      <el-option label="整型" value="INT"></el-option>
                      <el-option label="浮点型" value="FLOAT"></el-option>
                      <el-option label="字符串" value="VARCHAR"></el-option>
                      <el-option label="日期" value="DATETIME"></el-option>
                    </el-select>
                  </el-form-item>
                </template>
              </el-table-column>

              <!-- <el-table-column label="主键" align="center">
                <template slot-scope="scope">

                  <el-form-item>
                    <el-radio
                      :label="scope.$index"
                      v-model="radio"
                      class="textRadio"
                      >{{&nbsp;}}</el-radio
                    >
                  </el-form-item>
                </template>
              </el-table-column> -->
              <el-table-column label="不可为空" align="center">
                <template slot-scope="scope">
                  <!-- class="textRadio" -->
                  <el-form-item
                    :prop="'response_datas.' + scope.$index + '.field_NN'"
                    :rules="[{ required: true, message: '类型不能为空' }]"
                  >
                    <el-checkbox v-model="scope.row.field_NN"></el-checkbox>
                  </el-form-item>
                </template>
              </el-table-column>
              <el-table-column label="自增" align="center">
                <template slot-scope="scope">
                  <!-- class="textRadio" -->
                  <el-form-item
                    :prop="'response_datas.' + scope.$index + '.field_AI'"
                    :rules="[{ required: true, message: '类型不能为空' }]"
                    v-show="scope.row.field_type=='INT'"
                  >
                    <el-checkbox v-model="scope.row.field_AI"></el-checkbox>
                  </el-form-item>
                </template>
              </el-table-column>

              <el-table-column label="操作" width="80" align="center">
                <template slot-scope="scope">
                  <el-form-item>
                    <el-button
                      size="small"
                      type="danger"
                      plain
                      @click="handleDelete(scope.$index, scope.row)"
                      >删除</el-button
                    >
                  </el-form-item>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-row>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import qs from "qs";
import { mapState, mapActions } from "vuex";

export default {
  name: "ProjectBOM",
  components: {
    ELTable,
  },

  computed: {
    ...mapState(["CurrentUserInfo", "WorkCodeNameList"]),
  },
  data() {
    return {
      // 筛选
      listQuery: {
        table_name: "",
        work_code: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        table_name: "",
        work_code: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增人员弹出层
      productIdReadonly: false,
      dialogTitle: "",
      dialogFlag: false,
      radio: 0,
      dialogForm: {
        table_code: "",
        table_name: "",
        work_code: "",
        description: "",
        response_datas: [
          {
            field_name: "",
            field_type: "",
            field_PK: false,
            field_NN: false,
            field_AI: false,
          },
        ],
      },
      dialogFormBase: {
        table_code: "",
        table_name: "",
        work_code: "",
        description: "",
        response_datas: [
          {
            field_name: "",
            field_type: "",
            field_PK: false,
            field_NN: false,
            field_AI: false,
          },
        ],
      },
      dialogFormRules: {
        table_name: [
          { required: true, message: "请输入数据表编码", trigger: "blur" },
          { min: 3, max: 18, message: "长度为3~18个字节!", trigger: "blur" },
          {
            trigger: "blur",
            validator: (rule, value, callback) => {
              var productId = /^[a-zA-Z0-9-_]+$/;
              console.log(productId.test(value));
              if (!productId.test(value)) {
                callback(new Error("仅允许大小写字母和 '-' '_' !"));
              } else {
                callback();
              }
            },
          },
        ],

        work_code: [
          { required: true, message: "请选择工站", trigger: "change" },
        ],
      },
      colDeleteBtnFlag: false,
      // 表格列
      tableCol: [
        {
          label: "数据表名称",
          field: "table_name",
          align: "center",
        },
        {
          label: "工站名称",
          field: "work_name",
          align: "center",
        },
        {
          label: "备注",
          field: "description",
          align: "center",
        },
        {
          label: "创建时间",
          field: "create_time",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },
  methods: {
    ...mapActions(["getLogisticWorkCodeName"]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticWorkManage(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticWorkManage(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticWorkManage(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticWorkManage(this.listQuery);
    },

    // 数据表结构 查询
    getLogisticWorkManage(params) {
      baseManagement.getLogisticWorkManage(params).then((res) => {
        console.log("数据表结构 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            this.WorkCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.work_code == CodeNameListItem.work_code) {
                tableDataItem.work_name = CodeNameListItem.work_id+"_"+CodeNameListItem.work_name;
              }
            });
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 数据表结构 新增
    postLogisticWorkManage(params) {
      baseManagement.postLogisticWorkManage(params).then((res) => {
        console.log("数据表结构 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticWorkManage(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 数据表结构 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改数据表";
      this.dialogFlag = true;
      this.productIdReadonly = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));

      let rowData_response_datas = JSON.parse(
        JSON.stringify(rowData.response_datas)
      );
      rowData_response_datas.forEach((item) => {
        if (item.field_PK == "True") {
          item.field_PK = true;
        } else {
          item.field_PK = false;
        }
        if (item.field_NN == "True") {
          item.field_NN = true;
        } else {
          item.field_NN = false;
        }
        if (item.field_AI == "True") {
          item.field_AI = true;
        } else {
          item.field_AI = false;
        }
      });
      this.dialogForm = {
        table_code: rowData.table_code,
        table_name: rowData.table_name,
        work_code: rowData.work_code,
        description: rowData.description,
        response_datas: rowData_response_datas,
      };
    },

    getLogisticPutWorkManage(params) {
      baseManagement.getLogisticPutWorkManage(params).then((res) => {
        console.log("修改数据表 修改", res);
        if (res.code == 0) {
         
          this.getLogisticWorkManage(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 新增
    addBtn() {
      this.dialogTitle = "新增数据表";
      this.dialogFlag = true;
      this.productIdReadonly = false;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },
    //点击加号添加一行数据
    AddListRow() {
      this.dialogForm.response_datas.push({
        field_name: "",
        field_type: "",
        field_PK: false,
        field_NN: false,
        field_AI: false,
      });
    },
    // 弹出层确定
    dialogSave() {
      this.dialogForm.response_datas[this.radio].field_PK = true;
      console.log("123123123123213", this.dialogForm);
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //   return false;
          //验证通过调用保存接口
          if (this.dialogTitle == "新增数据表") {
            console.log("新增数据表", this.dialogForm);
            this.postLogisticWorkManage(this.dialogForm);
          } else {
            let params = JSON.parse(JSON.stringify(this.dialogForm));
            params.response_datas.forEach((item) => {
              if (item.field_PK) {
                item.field_PK = "True";
              } else {
                item.field_PK = "False";
              }
              if (item.field_NN) {
                item.field_NN = "True";
              } else {
                item.field_NN = "False";
              }
              if (item.field_AI) {
                item.field_AI = "True";
              } else {
                item.field_AI = "False";
              }
            });
            params.response_datas = JSON.stringify(params.response_datas);

            console.log("修改数据表", params);
            this.getLogisticPutWorkManage(params);
          }
        }
      });
    },

    // 数据表结构 删除
    getLogisticDeleteWorkManage(params) {
      baseManagement.getLogisticDeleteWorkManage(params).then((res) => {
        console.log("数据表结构 删除", res);
        if (res.code == 0) {
          this.getLogisticWorkManage(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 点击删除当前行
    handleDelete(index, rowData) {
      if (rowData.bom_matter_code) {
        this.$confirm("确定执行删除操作吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(() => {
          let params = {
            prodect_code: rowData.prodect_code,
            response_datas: JSON.stringify([
              { field_code: rowData.field_code },
            ]),
          };
          this.getLogisticDeleteWorkManage(params);
          this.dialogForm.response_datas.splice(index, 1);
        });
      } else {
        this.dialogForm.response_datas.splice(index, 1);
      }
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let params = {
          table_code: rowData.table_code,
          response_datas: JSON.stringify([]),
        };
        this.getLogisticDeleteWorkManage(params);
      });
    },
  },
  mounted() {
    console.log("当前登陆人的接口 数据表结构", this.CurrentUserInfo);

    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    // 数据表结构 查询
    this.getLogisticWorkManage(this.listQuery);
  },
};
</script>
<style lang="scss" scoped>
.dialogTable {
  .el-form-item {
    // margin: 0;
    /deep/ .el-form-item__content {
      margin: 0 !important;
    }
  }
}
</style>